
.button-variant(@color; @background; @border) {
  color: @color;
  background-color: @background;
  border-color: @border;

  &:hover,
  &:focus,
  &.binf-focus,
  &:active,
  &.binf-active{
    color: @color;
    background-color: darken(@background, 10%);
    border-color: darken(@border, 12%);
  }
  .binf-dropdown-toggle&:hover,
  .binf-dropdown-toggle&:active,
  .binf-dropdown-toggle&:focus,
  .binf-dropdown-toggle&.binf-active,
  .binf-dropdown-toggle& {
    background-color: darken(@background, 0%);
  }
  &:active,
  &.binf-active,
  .binf-open > .binf-dropdown-toggle& {
    background-image: none;
  }
  &.binf-disabled,
  &[disabled],
  fieldset[disabled] & {
    &,
    &:hover,
    &:focus,
    &.binf-focus,
    &:active,
    &.binf-active {
      background-color: @background;
          border-color: @border;
    }
  }

  .binf-badge {
    color: @background;
    background-color: @color;
  }
}

// Button sizes
.button-size(@padding-vertical; @padding-horizontal; @font-size; @line-height; @border-radius) {
  padding: @padding-vertical @padding-horizontal;
  font-size: @font-size;
  line-height: @line-height;
  border-radius: @border-radius;
}
