

.binf-list-group {
  margin-bottom: 20px;
  padding-left: 0;
}



.binf-list-group-item {
  position: relative;
  display: block;
  padding: 10px 15px;
  // Place the border on the list items and negative margin up for better styling
  margin-bottom: -1px;
  background-color: @list-group-bg;
  border: 1px solid @list-group-border;

  // Round the first and last items
  &:first-child {
    .border-top-radius(@list-group-border-radius);
  }
  &:last-child {
    margin-bottom: 0;
    .border-bottom-radius(@list-group-border-radius);
  }
}



a.binf-list-group-item {
  color: @list-group-link-color;

  .binf-list-group-item-heading {
    color: @list-group-link-heading-color;
  }

  // Hover state
  &:hover,
  &:focus {
    text-decoration: none;
    color: @list-group-link-hover-color;
    background-color: @list-group-hover-bg;
  }
}

.binf-list-group-item {
  // Disabled state
  &.binf-disabled,
  &.binf-disabled:hover,
  &.binf-disabled:focus {
    background-color: @list-group-disabled-bg;
    color: @list-group-disabled-color;
    cursor: @cursor-disabled;

    // Force color to inherit for custom content
    .binf-list-group-item-heading {
      color: inherit;
    }
    .binf-list-group-item-text {
      color: @list-group-disabled-text-color;
    }
  }

  // Active class on item itself, not parent
  &.binf-active,
  &.binf-active:hover,
  &.binf-active:focus {
    z-index: 2; // Place active items above their siblings for proper border styling
    color: @list-group-active-color;
    background-color: @list-group-active-bg;
    border-color: @list-group-active-border;

    // Force color to inherit for custom content
    .binf-list-group-item-heading,
    .binf-list-group-item-heading > small,
    .binf-list-group-item-heading > .binf-small {
      color: inherit;
    }
    .binf-list-group-item-text {
      color: @list-group-active-text-color;
    }
  }
}



.list-group-item-variant(binf-success; @state-success-bg; @state-success-text);
.list-group-item-variant(binf-info; @state-info-bg; @state-info-text);
.list-group-item-variant(binf-warning; @state-warning-bg; @state-warning-text);
.list-group-item-variant(binf-danger; @state-danger-bg; @state-danger-text);



.binf-list-group-item-heading {
  margin-top: 0;
  margin-bottom: 5px;
}
.binf-list-group-item-text {
  margin-bottom: 0;
  line-height: 1.3;
}
