

.binf-container {
  .container-fixed();

  @media (min-width: @screen-sm-min) {
    width: @container-sm;
  }
  @media (min-width: @screen-md-min) {
    width: @container-md;
  }
  @media (min-width: @screen-lg-min) {
    width: @container-lg;
  }
}



.binf-container-fluid {
  .container-fixed();
}

.binf-row {
  .make-row();
}



.make-grid-columns();



.make-grid(xs);



@media (min-width: @screen-sm-min) {
  .make-grid(sm);
}


@media (min-width: @screen-md-min) {
  .make-grid(md);
}


@media (min-width: @screen-lg-min) {
  .make-grid(lg);
}
