
.binf-carousel {
  position: relative;
}

.binf-carousel-inner {
  position: relative;
  overflow: hidden;
  width: 100%;

  > .binf-item {
    display: none;
    position: relative;
    .transition(.6s ease-in-out left);

    // Account for jankitude on images
    > img,
    > a > img {
      &:extend(.binf-img-responsive);
      line-height: 1;
    }

    // WebKit CSS3 transforms for supported devices
    @media all and (transform-3d), (-webkit-transform-3d) {
      .transition-transform(~'0.6s ease-in-out');
      .backface-visibility(~'hidden');
      .perspective(1000);

      &.binf-next,
      &.binf-active.binf-right {
        .translate3d(100%, 0, 0);
        left: 0;
      }
      &.binf-prev,
      &.binf-active.binf-left {
        .translate3d(-100%, 0, 0);
        left: 0;
      }
      &.binf-next.binf-left,
      &.binf-prev.binf-right,
      &.binf-active {
        .translate3d(0, 0, 0);
        left: 0;
      }
    }
  }

  > .binf-active,
  > .binf-next,
  > .binf-prev {
    display: block;
  }

  > .binf-active {
    left: 0;
  }

  > .binf-next,
  > .binf-prev {
    position: absolute;
    top: 0;
    width: 100%;
  }

  > .binf-next {
    left: 100%;
  }
  > .binf-prev {
    left: -100%;
  }
  > .binf-next.binf-left,
  > .binf-prev.binf-right {
    left: 0;
  }

  > .binf-active.binf-left {
    left: -100%;
  }
  > .binf-active.binf-right {
    left: 100%;
  }

}

// Left/right controls for nav
// ---------------------------

.binf-carousel-control {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: @carousel-control-width;
  .opacity(@carousel-control-opacity);
  font-size: @carousel-control-font-size;
  color: @carousel-control-color;
  text-align: center;
  text-shadow: @carousel-text-shadow;

  // Set gradients for backgrounds
  &.binf-left {
    #gradient > .horizontal(@start-color: rgba(0,0,0,.5); @end-color: rgba(0,0,0,.0001));
  }
  &.binf-right {
    left: auto;
    right: 0;
    #gradient > .horizontal(@start-color: rgba(0,0,0,.0001); @end-color: rgba(0,0,0,.5));
  }

  // Hover/focus state
  &:hover,
  &:focus {
    outline: 0;
    color: @carousel-control-color;
    text-decoration: none;
    .opacity(.9);
  }

  // Toggles
  .binf-icon-prev,
  .binf-icon-next,
  .binf-glyphicon-chevron-left,
  .binf-glyphicon-chevron-right {
    position: absolute;
    top: 50%;
    z-index: 5;
    display: inline-block;
  }
  .binf-icon-prev,
  .binf-glyphicon-chevron-left {
    left: 50%;
    margin-left: -10px;
  }
  .binf-icon-next,
  .binf-glyphicon-chevron-right {
    right: 50%;
    margin-right: -10px;
  }
  .binf-icon-prev,
  .binf-icon-next {
    width:  20px;
    height: 20px;
    margin-top: -10px;
    line-height: 1;
    font-family: serif;
  }


  .binf-icon-prev {
    &:before {
      content: '\2039';// SINGLE LEFT-POINTING ANGLE QUOTATION MARK (U+2039)
    }
  }
  .binf-icon-next {
    &:before {
      content: '\203a';// SINGLE RIGHT-POINTING ANGLE QUOTATION MARK (U+203A)
    }
  }
}


.binf-carousel-indicators {
  position: absolute;
  bottom: 10px;
  left: 50%;
  z-index: 15;
  width: 60%;
  margin-left: -30%;
  padding-left: 0;
  list-style: none;
  text-align: center;

  li {
    display: inline-block;
    width:  10px;
    height: 10px;
    margin: 1px;
    text-indent: -999px;
    border: 1px solid @carousel-indicator-border-color;
    border-radius: 10px;
    cursor: pointer;

    background-color: #000 \9; // IE8
    background-color: rgba(0,0,0,0); // IE9
  }
  .binf-active {
    margin: 0;
    width:  12px;
    height: 12px;
    background-color: @carousel-indicator-active-bg;
  }
}

// Optional captions
// -----------------------------
// Hidden by default for smaller viewports
.binf-carousel-caption {
  position: absolute;
  left: 15%;
  right: 15%;
  bottom: 20px;
  z-index: 10;
  padding-top: 20px;
  padding-bottom: 20px;
  color: @carousel-caption-color;
  text-align: center;
  text-shadow: @carousel-text-shadow;
  & .binf-btn {
    text-shadow: none; // No shadow for button elements in carousel-caption
  }
}


// Scale up controls for tablets and up
@media screen and (min-width: @screen-sm-min) {

  // Scale up the controls a smidge
  .binf-carousel-control {
    .binf-glyphicon-chevron-left,
    .binf-glyphicon-chevron-right,
    .binf-icon-prev,
    .binf-icon-next {
      width: 30px;
      height: 30px;
      margin-top: -15px;
      font-size: 30px;
    }
    .binf-glyphicon-chevron-left,
    .binf-icon-prev {
      margin-left: -15px;
    }
    .binf-glyphicon-chevron-right,
    .binf-icon-next {
      margin-right: -15px;
    }
  }

  // Show and left align the captions
  .binf-carousel-caption {
    left: 20%;
    right: 20%;
    padding-bottom: 30px;
  }

  // Move up the indicators
  .binf-carousel-indicators {
    bottom: 20px;
  }
}
