//
// Utility classes
// --------------------------------------------------


// Floats
// -------------------------

.binf-clearfix {
  .binf-clearfix();
}
.binf-center-block {
  .center-block();
}
.binf-pull-right {
  float: right !important;
}
.binf-pull-left {
  float: left !important;
}


.binf-hide {
  display: none !important;
}
.binf-show {
  display: block !important;
}
.binf-invisible {
  visibility: hidden;
}
.binf-text-hide {
  .text-hide();
}

// Flex support
// -------------------------
.binf-flex-row {
  display: flex;
  flex-direction: row;
  min-width: 0; /* allows Safari, Firefox,etc. to shrink to less than it's intrinsic size */
}

.binf-flex-column {
  display: flex;
  flex-direction: column;
  min-height: 0; /* allows Safari, Firefox,etc. to shrink to less than it's intrinsic size */
}

// Hide from screenreaders and browsers
//
// Credit: HTML5 Boilerplate

.binf-hidden {
  display: none !important;
  visibility: hidden !important;
}


// For Affix plugin
// -------------------------

.binf-affix {
  position: fixed;
}

.binf-line-clamp {
  .css-line-clamp();
}

// for IE11 in both LTR and RTL directions
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .binf-line-clamp {
    .unsupport-css-line-clamp();
  }
}