//
// Panels
// --------------------------------------------------


// Base class
.binf-panel {
  margin-bottom: @line-height-computed;
  background-color: @panel-bg;
  border: 1px solid transparent;
  border-radius: @panel-border-radius;
  .box-shadow(0 1px 1px rgba(0,0,0,.05));
}

// Panel contents
.binf-panel-body {
  padding: @panel-body-padding;
  &:extend(.binf-clearfix all);
}

// Optional heading
.binf-panel-heading {
  padding: @panel-heading-padding;
  border-bottom: 1px solid transparent;
  .border-top-radius((@panel-border-radius - 1));

  > .binf-dropdown .binf-dropdown-toggle {
    color: inherit;
  }
}


.binf-panel-title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: ceil((@font-size-base * 1.125));
  color: inherit;

  > a,
  > small,
  > .binf-small,
  > small > a,
  > .binf-small > a {
    color: inherit;
  }
}

// Optional footer (stays gray in every modifier class)
.binf-panel-footer {
  padding: @panel-footer-padding;
  background-color: @panel-footer-bg;
  border-top: 1px solid @panel-inner-border;
  .border-bottom-radius((@panel-border-radius - 1));
}



.binf-panel {
  > .binf-list-group,
  > .binf-panel-collapse > .binf-list-group {
    margin-bottom: 0;

    .binf-list-group-item {
      border-width: 1px 0;
      border-radius: 0;
    }

    // Add border top radius for first one
    &:first-child {
      .binf-list-group-item:first-child {
        border-top: 0;
        .border-top-radius((@panel-border-radius - 1));
      }
    }
    // Add border bottom radius for last one
    &:last-child {
      .binf-list-group-item:last-child {
        border-bottom: 0;
        .border-bottom-radius((@panel-border-radius - 1));
      }
    }
  }
}

.binf-panel-heading + .binf-list-group {
  .binf-list-group-item:first-child {
    border-top-width: 0;
  }
}
.binf-list-group + .binf-panel-footer {
  border-top-width: 0;
}

.binf-panel {
  > .binf-table,
  > .binf-table-responsive > .binf-table,
  > .binf-panel-collapse > .binf-table {
    margin-bottom: 0;

    caption {
      padding-left: @panel-body-padding;
      padding-right: @panel-body-padding;
    }
  }
  // Add border top radius for first one
  > .binf-table:first-child,
  > .binf-table-responsive:first-child > .binf-table:first-child {
    .border-top-radius((@panel-border-radius - 1));

    > thead:first-child,
    > tbody:first-child {
      > tr:first-child {
        border-top-left-radius: (@panel-border-radius - 1);
        border-top-right-radius: (@panel-border-radius - 1);

        td:first-child,
        th:first-child {
          border-top-left-radius: (@panel-border-radius - 1);
        }
        td:last-child,
        th:last-child {
          border-top-right-radius: (@panel-border-radius - 1);
        }
      }
    }
  }
  // Add border bottom radius for last one
  > .binf-table:last-child,
  > .binf-table-responsive:last-child > .binf-table:last-child {
    .border-bottom-radius((@panel-border-radius - 1));

    > tbody:last-child,
    > tfoot:last-child {
      > tr:last-child {
        border-bottom-left-radius: (@panel-border-radius - 1);
        border-bottom-right-radius: (@panel-border-radius - 1);

        td:first-child,
        th:first-child {
          border-bottom-left-radius: (@panel-border-radius - 1);
        }
        td:last-child,
        th:last-child {
          border-bottom-right-radius: (@panel-border-radius - 1);
        }
      }
    }
  }
  > .binf-panel-body + .binf-table,
  > .binf-panel-body + .binf-table-responsive,
  > .binf-table + .binf-panel-body,
  > .binf-table-responsive + .binf-panel-body {
    border-top: 1px solid @table-border-color;
  }
  > .binf-table > tbody:first-child > tr:first-child th,
  > .binf-table > tbody:first-child > tr:first-child td {
    border-top: 0;
  }
  > .binf-table-bordered,
  > .binf-table-responsive > .binf-table-bordered {
    border: 0;
    > thead,
    > tbody,
    > tfoot {
      > tr {
        > th:first-child,
        > td:first-child {
          border-left: 0;
        }
        > th:last-child,
        > td:last-child {
          border-right: 0;
        }
      }
    }
    > thead,
    > tbody {
      > tr:first-child {
        > td,
        > th {
          border-bottom: 0;
        }
      }
    }
    > tbody,
    > tfoot {
      > tr:last-child {
        > td,
        > th {
          border-bottom: 0;
        }
      }
    }
  }
  > .binf-table-responsive {
    border: 0;
    margin-bottom: 0;
  }
}

.binf-panel-group {
  margin-bottom: @line-height-computed;

  // Tighten up margin so it's only between panels
  .binf-panel {
    margin-bottom: 0;
    border-radius: @panel-border-radius;

    + .binf-panel {
      margin-top: 5px;
    }
  }

  .binf-panel-heading {
    border-bottom: 0;

    + .binf-panel-collapse > .binf-panel-body,
    + .binf-panel-collapse > .binf-list-group {
      border-top: 1px solid @panel-inner-border;
    }
  }

  .binf-panel-footer {
    border-top: 0;
    + .binf-panel-collapse .binf-panel-body {
      border-bottom: 1px solid @panel-inner-border;
    }
  }
}


// Contextual variations
.binf-panel-default {
  .panel-variant(@panel-default-border; @panel-default-text; @panel-default-heading-bg; @panel-default-border);
}
.binf-panel-primary {
  .panel-variant(@panel-primary-border; @panel-primary-text; @panel-primary-heading-bg; @panel-primary-border);
}
.binf-panel-success {
  .panel-variant(@panel-success-border; @panel-success-text; @panel-success-heading-bg; @panel-success-border);
}
.binf-panel-info {
  .panel-variant(@panel-info-border; @panel-info-text; @panel-info-heading-bg; @panel-info-border);
}
.binf-panel-warning {
  .panel-variant(@panel-warning-border; @panel-warning-text; @panel-warning-heading-bg; @panel-warning-border);
}
.binf-panel-danger {
  .panel-variant(@panel-danger-border; @panel-danger-text; @panel-danger-heading-bg; @panel-danger-border);
}
