
@-ms-viewport {
  width: device-width;
}


.binf-visible-xs,
.binf-visible-sm,
.binf-visible-md,
.binf-visible-lg {
  .responsive-invisibility();
}

.binf-visible-xs-block,
.binf-visible-xs-inline,
.binf-visible-xs-inline-block,
.binf-visible-sm-block,
.binf-visible-sm-inline,
.binf-visible-sm-inline-block,
.binf-visible-md-block,
.binf-visible-md-inline,
.binf-visible-md-inline-block,
.binf-visible-lg-block,
.binf-visible-lg-inline,
.binf-visible-lg-inline-block {
  display: none !important;
}

.binf-visible-xs {
  @media (max-width: @screen-xs-max) {
    .responsive-visibility();
  }
}
.binf-visible-xs-block {
  @media (max-width: @screen-xs-max) {
    display: block !important;
  }
}
.binf-visible-xs-inline {
  @media (max-width: @screen-xs-max) {
    display: inline !important;
  }
}
.binf-visible-xs-inline-block {
  @media (max-width: @screen-xs-max) {
    display: inline-block !important;
  }
}

.binf-visible-sm {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    .responsive-visibility();
  }
}
.binf-visible-sm-block {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    display: block !important;
  }
}
.binf-visible-sm-inline {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    display: inline !important;
  }
}
.binf-visible-sm-inline-block {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    display: inline-block !important;
  }
}

.binf-visible-md {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    .responsive-visibility();
  }
}
.binf-visible-md-block {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    display: block !important;
  }
}
.binf-visible-md-inline {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    display: inline !important;
  }
}
.binf-visible-md-inline-block {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    display: inline-block !important;
  }
}

.binf-visible-lg {
  @media (min-width: @screen-lg-min) {
    .responsive-visibility();
  }
}
.binf-visible-lg-block {
  @media (min-width: @screen-lg-min) {
    display: block !important;
  }
}
.binf-visible-lg-inline {
  @media (min-width: @screen-lg-min) {
    display: inline !important;
  }
}
.binf-visible-lg-inline-block {
  @media (min-width: @screen-lg-min) {
    display: inline-block !important;
  }
}

.binf-hidden-xs {
  @media (max-width: @screen-xs-max) {
    .responsive-invisibility();
  }
}
.binf-hidden-sm {
  @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
    .responsive-invisibility();
  }
}
.binf-hidden-md {
  @media (min-width: @screen-md-min) and (max-width: @screen-md-max) {
    .responsive-invisibility();
  }
}
.binf-hidden-lg {
  @media (min-width: @screen-lg-min) {
    .responsive-invisibility();
  }
}
