.binf-media {
  // Proper spacing between instances of .media
  margin-top: 15px;

  &:first-child {
    margin-top: 0;
  }
}

.binf-media,
.binf-media-body {
  zoom: 1;
  overflow: hidden;
}

.binf-media-body {
  width: 10000px;
}

.binf-media-object {
  display: block;
}

.binf-media-right,
.binf-media > .binf-pull-right {
  padding-left: 10px;
}

.binf-media-left,
.binf-media > .binf-pull-left {
  padding-right: 10px;
}

.binf-media-left,
.binf-media-right,
.binf-media-body {
  display: table-cell;
  vertical-align: top;
}

.binf-media-middle {
  vertical-align: middle;
}

.binf-media-bottom {
  vertical-align: bottom;
}

// Reset margins on headings for tighter default spacing
.binf-media-heading {
  margin-top: 0;
  margin-bottom: 5px;
}


.binf-media-list {
  padding-left: 0;
  list-style: none;
}
