

// Dropdown arrow/caret
.binf-caret {
  display: inline-block;
  width: 0;
  height: 0;
  margin-left: 2px;
  vertical-align: middle;
  border-top:   @caret-width-base solid;
  border-right: @caret-width-base solid transparent;
  border-left:  @caret-width-base solid transparent;
}

// The dropdown wrapper (div)
.binf-dropup,
.binf-dropdown {
  position: relative;
}


// The dropdown menu (ul)
.binf-dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  z-index: @zindex-dropdown;
  display: none; // none by default, but block on "open" of the menu
  float: left;
  min-width: 192px;
  max-width: 240px;
  width: max-content;
  padding: 3px 0;
  margin: 2px 0 0; // override default ul
  list-style: none;
  font-size: @font-size-base;
  text-align: left;
  background-color: @dropdown-bg;
  border: 1px solid @dropdown-fallback-border; // IE8 fallback
  border: 1px solid @dropdown-border;
  border-radius: @border-radius-base;
  .box-shadow(0 6px 12px rgba(0,0,0,.175));
  background-clip: padding-box;


  &.binf-pull-right {
    right: 0;
    left: auto;
  }

  // Dividers (basically an hr) within the dropdown
  .binf-divider {
    .nav-divider(@dropdown-divider-bg);
  }

  > li {
    margin: 1px 0;
  }

  // Links within the dropdown menu
  > li > a {
    display: block;
    padding: 6px 16px;
    clear: both;
    font-weight: normal;
    line-height: @line-height-base;
    color: @dropdown-link-color;
    white-space: normal; // breaking longer links into new/next lines.
  }
}

// Hover/Focus state
.binf-dropdown-menu > li > a {
  &:hover,
  &:focus {
    text-decoration: none;
    color: @dropdown-link-color;    /*all dropdowns are using dropdown-link-color for hover state instate of dropdown-link-hover-color, so modified globally*/
    background-color: @dropdown-link-hover-bg;
  }
}

// Active state
.binf-dropdown-menu > .binf-active > a {
  &,
  &:hover,
  &:focus {
    color: @dropdown-link-active-color;
    text-decoration: none;
    outline: 0;
    background-color: @dropdown-link-active-bg;
  }
}

// Disabled state
//
// Gray out text and ensure the hover/focus state remains gray

.binf-dropdown-menu > .binf-disabled > a {
  &,
  &:hover,
  &:focus {
    color: @dropdown-link-disabled-color;
  }

  // Nuke hover/focus effects
  &:hover,
  &:focus {
    text-decoration: none;
    background-color: transparent;
    background-image: none; // Remove CSS gradient
    .reset-filter();
    cursor: @cursor-disabled;
  }
}

// Open state for the dropdown
.binf-open {
  // Show the menu
  > .binf-dropdown-menu {
    display: block;
  }

  // Remove the outline when :focus is triggered
  > a {
    outline: 0;
  }
}

.binf-dropdown-menu-right {
  left: auto;
  right: 0;
}
.binf-dropdown-menu-left {
  left: 0;
  right: auto;
}

// Dropdown section headers
.binf-dropdown-header {
  display: block;
  padding: 6px 16px;
  font-size: @font-size-small;
  line-height: @line-height-small;
  color: @dropdown-header-color;
  white-space: nowrap; // as with > li > a
  margin-bottom: 2px;
}

// Backdrop to catch body clicks on mobile, etc.
.binf-dropdown-backdrop {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: (@zindex-dropdown - 10);
}

// Right aligned dropdowns
.binf-pull-right > .binf-dropdown-menu {
  right: 0;
  left: auto;
}

.binf-dropup,
.binf-navbar-fixed-bottom .binf-dropdown {
  // Reverse the caret
  .binf-caret {
    border-top: 0;
    border-bottom: @caret-width-base solid;
    content: "";
  }
  // Different positioning for bottom up menu
  .binf-dropdown-menu {
    top: auto;
    bottom: 100%;
    margin-bottom: 2px;
  }
}

li.binf-dropdown-submenu,
.binf-dropdown-submenu {
  > a {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.binf-dropdown-submenu {
  position: relative;
  > a:after {
    display: block;
    content: " ";
    float: right;
    width: 0;
    height: 0;
    border-color: transparent;
    border-style: solid;
    border-width: 5px 0 5px 5px;
    border-left-color: @dropdown-caret-color;
  }
  &.binf-open {
    > a {
      color: @dropdown-link-color;
      background-color: @dropdown-link-hover-bg;
    }
    > .binf-dropdown-menu,
    &.binf-dropdown > .binf-dropdown-menu { // DEFAULT PULL RIGHT TOWARDS DOWN
      top: 0;
      bottom: auto;
    }
    &.binf-dropup > .binf-dropdown-menu {
      top: auto;
      bottom: 0;
    }
    > .binf-dropdown-menu,
    &.binf-dropdown-menu-right > .binf-dropdown-menu {
      left: calc(100% - 4px);
    }
    &.binf-dropdown-menu-left {
      float: none;
      > .binf-dropdown-menu {
        left: calc(-100% + 4px);
      }
    }
  }
}




@media (min-width: @grid-float-breakpoint) {
  .binf-navbar-right {
    .binf-dropdown-menu {
      .binf-dropdown-menu-right();
    }
    .binf-dropdown-menu-left {
      .binf-dropdown-menu-left();
    }
  }
}
