
.binf-fade {
  opacity: 0;
  .transition(opacity .15s linear);
  &.binf-in {
    opacity: 1;
  }
}

.binf-collapse {
  display: none;
  visibility: hidden;

  &.binf-in      { display: block; visibility: visible; }
  tr&.binf-in    { display: table-row; }
  tbody&.binf-in { display: table-row-group; }
}

.binf-collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  .transition-property(~"height, visibility");
  .transition-duration(.35s);
  .transition-timing-function(ease);
}
