
.binf-modal-open {
  overflow: hidden;
}

// Container that the modal scrolls within
.binf-modal {
  display: none;
  overflow: hidden;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: @zindex-modal;
  -webkit-overflow-scrolling: touch;

  outline: 0;

  // When fading in the modal, animate it to slide down
  &.binf-fade .binf-modal-dialog {
    .translate(0, -25%);
    .transition-transform(~"0.3s ease-out");
  }
  &.binf-in .binf-modal-dialog { .translate(0, 0) }
}
.binf-modal-open .binf-modal {
  overflow-x: hidden;
  overflow-y: auto;
}

// Shell div to position the modal with bottom padding
.binf-modal-dialog {
  position: relative;
  width: auto;
  margin: 10px;
}

// Actual modal
.binf-modal-content {
  position: relative;
  background-color: @modal-content-bg;
  border: 1px solid @modal-content-fallback-border-color; //old browsers fallback (ie8 etc)
  border: 1px solid @modal-content-border-color;
  border-radius: @border-radius-large;
  .box-shadow(0 3px 9px rgba(0,0,0,.5));
  background-clip: padding-box;
  // Remove focus outline from opened modal
  outline: 0;
}

// Modal background
.binf-modal-backdrop {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  background-color: @modal-backdrop-bg;
  // Fade for backdrop
  &.binf-fade { .opacity(0); }
  &.binf-in { .opacity(@modal-backdrop-opacity); }
}

// Modal header
// Top section of the modal w/ title and dismiss
.binf-modal-header {
  padding: @modal-title-padding;
  border-bottom: 1px solid @modal-header-border-color;
  min-height: (@modal-title-padding + @modal-title-line-height);
}
// Close icon
.binf-modal-header .binf-close {
  margin-top: -2px;
}

// Title text within header
.binf-modal-title {
  margin: 0;
  line-height: @modal-title-line-height;
}


.binf-modal-body {
  position: relative;
  padding: @modal-inner-padding;
}

// Footer (for actions)
.binf-modal-footer {
  padding: @modal-inner-padding;
  text-align: right; // right align buttons
  border-top: 1px solid @modal-footer-border-color;
  &:extend(.binf-clearfix all);

  // Properly space out buttons
  .binf-btn + .binf-btn {
    margin-left: 5px;
    margin-bottom: 0; // account for input[type="submit"] which gets the bottom margin like all other inputs
  }
  // but override that for button groups
  .binf-btn-group .binf-btn + .binf-btn {
    margin-left: -1px;
  }
  // and override it for block buttons as well
  .binf-btn-block + .binf-btn-block {
    margin-left: 0;
  }
}

// Measure scrollbar width for padding body during modal show/hide
.binf-modal-scrollbar-measure {
  position: absolute;
  top: -9999px;
  width: 50px;
  height: 50px;
  overflow: scroll;
}

// Scale up the modal
@media (min-width: @screen-sm-min) {
  // Automatically set modal's width for larger viewports
  .binf-modal-dialog {
    width: @modal-md;
    margin: 30px auto;
  }
  .binf-modal-content {
    .box-shadow(0 5px 15px rgba(0,0,0,.5));
  }

  // Modal sizes
  .binf-modal-sm { width: @modal-sm; }
}

@media (min-width: @screen-md-min) {
  .binf-modal-lg { width: @modal-lg; }
}
