// Text overflow
// Requires inline-block or block for proper styling

.text-overflow() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// css-line-clamp: apply ellipses after given number of lines.

.css-line-clamp() {
  // OpenText
  display: -webkit-box;
  -webkit-line-clamp: 2; /* this can be vary based on the requirement, as a default giving 2 lines */
  -webkit-box-orient: vertical;
  overflow: hidden;
  word-break: keep-all;
}

// unsupport-css-line-clamp: display in single line and/or apply ellipses if it overflows.
.unsupport-css-line-clamp() {
  // OpenText
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: block;
}