
.binf-btn-group,
.binf-btn-group-vertical {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  > .binf-btn {
    position: relative;
    float: left;
    // Bring the "active" button to the front
    &:hover,
    &:focus,
    &:active,
    &.binf-active {
      z-index: 2;
    }
  }
}

// Prevent double borders when buttons are next to each other
.binf-btn-group {
  .binf-btn + .binf-btn,
  .binf-btn + .binf-btn-group,
  .binf-btn-group + .binf-btn,
  .binf-btn-group + .binf-btn-group {
    margin-left: -1px;
  }
}

// Optional: Group multiple button groups together for a toolbar
.binf-btn-toolbar {
  margin-left: -5px; // Offset the first child's margin
  &:extend(.binf-clearfix all);

  .binf-btn-group,
  .binf-input-group {
    float: left;
  }
  > .binf-btn,
  > .binf-btn-group,
  > .binf-input-group {
    margin-left: 5px;
  }
}

.binf-btn-group > .binf-btn:not(:first-child):not(:last-child):not(.binf-dropdown-toggle) {
  border-radius: 0;
}

// Set corners individual because sometimes a single button can be in a .btn-group and we need :first-child and :last-child to both match
.binf-btn-group > .binf-btn:first-child {
  margin-left: 0;
  &:not(:last-child):not(.binf-dropdown-toggle) {
    .border-right-radius(0);
  }
}
// Need .dropdown-toggle since :last-child doesn't apply given a .dropdown-menu immediately after it
.binf-btn-group > .binf-btn:last-child:not(:first-child),
.binf-btn-group > .binf-dropdown-toggle:not(:first-child) {
  .border-left-radius(0);
}

// Custom edits for including btn-groups within btn-groups (useful for including dropdown buttons within a btn-group)
.binf-btn-group > .binf-btn-group {
  float: left;
}
.binf-btn-group > .binf-btn-group:not(:first-child):not(:last-child) > .binf-btn {
  border-radius: 0;
}
.binf-btn-group > .binf-btn-group:first-child:not(:last-child) {
  > .binf-btn:last-child,
  > .binf-dropdown-toggle {
    .border-right-radius(0);
  }
}
.binf-btn-group > .binf-btn-group:last-child:not(:first-child) > .binf-btn:first-child {
  .border-left-radius(0);
}

// On active and open, don't show outline
.binf-btn-group .binf-dropdown-toggle:active,
.binf-btn-group.binf-open .binf-dropdown-toggle {
  outline: 0;
}


// Sizing
//
// Remix the default button sizing classes into new ones for easier manipulation.

.binf-btn-group-xs > .binf-btn { &:extend(.binf-btn-xs); }
.binf-btn-group-sm > .binf-btn { &:extend(.binf-btn-sm); }
.binf-btn-group-lg > .binf-btn { &:extend(.binf-btn-lg); }


// Split button dropdowns
// ----------------------

// Give the line between buttons some depth
.binf-btn-group > .binf-btn + .binf-dropdown-toggle {
  padding-left: 8px;
  padding-right: 8px;
}
.binf-btn-group > .binf-btn-lg + .binf-dropdown-toggle {
  padding-left: 12px;
  padding-right: 12px;
}

// The clickable button for toggling the menu
// Remove the gradient and set the same inset shadow as the :active state
.binf-btn-group.binf-open .binf-dropdown-toggle {
  .box-shadow(inset 0 3px 5px rgba(0,0,0,.125));

  // Show no shadow for `.btn-link` since it has no other button styles.
  &.binf-btn-link {
    .box-shadow(none);
  }
}


// Reposition the caret
.binf-btn .binf-caret {
  margin-left: 0;
}
// Carets in other button sizes
.binf-btn-lg .binf-caret {
  border-width: @caret-width-large @caret-width-large 0;
  border-bottom-width: 0;
}
// Upside down carets for .binf-dropup
.binf-dropup .binf-btn-lg .binf-caret {
  border-width: 0 @caret-width-large @caret-width-large;
}


// Vertical button groups
// ----------------------

.binf-btn-group-vertical {
  > .binf-btn,
  > .binf-btn-group,
  > .binf-btn-group > .binf-btn {
    display: block;
    float: none;
    width: 100%;
    max-width: 100%;
  }

  // Clear floats so dropdown menus can be properly placed
  > .binf-btn-group {
    &:extend(.binf-clearfix all);
    > .binf-btn {
      float: none;
    }
  }

  > .binf-btn + .binf-btn,
  > .binf-btn + .binf-btn-group,
  > .binf-btn-group + .binf-btn,
  > .binf-btn-group + .binf-btn-group {
    margin-top: -1px;
    margin-left: 0;
  }
}

.binf-btn-group-vertical > .binf-btn {
  &:not(:first-child):not(:last-child) {
    border-radius: 0;
  }
  &:first-child:not(:last-child) {
    border-top-right-radius: @border-radius-base;
    .border-bottom-radius(0);
  }
  &:last-child:not(:first-child) {
    border-bottom-left-radius: @border-radius-base;
    .border-top-radius(0);
  }
}
.binf-btn-group-vertical > .binf-btn-group:not(:first-child):not(:last-child) > .binf-btn {
  border-radius: 0;
}
.binf-btn-group-vertical > .binf-btn-group:first-child:not(:last-child) {
  > .binf-btn:last-child,
  > .binf-dropdown-toggle {
    .border-bottom-radius(0);
  }
}
.binf-btn-group-vertical > .binf-btn-group:last-child:not(:first-child) > .binf-btn:first-child {
  .border-top-radius(0);
}


// Justified button groups
// ----------------------

.binf-btn-group-justified {
  display: table;
  width: 100%;
  table-layout: fixed;
  border-collapse: separate;
  > .binf-btn,
  > .binf-btn-group {
    float: none;
    display: table-cell;
    width: 1%;
  }
  > .binf-btn-group .binf-btn {
    width: 100%;
  }

  > .binf-btn-group .binf-dropdown-menu {
    left: auto;
  }
}



[data-binf-toggle="buttons"] {
  > .binf-btn,
  > .binf-btn-group > .binf-btn {
    input[type="radio"],
    input[type="checkbox"] {
      position: absolute;
      clip: rect(0,0,0,0);
      //pointer-events: none;
    }
  }
}
