
.binf-input-group {
  position: relative; // For dropdowns
  display: table;
  border-collapse: separate; // prevent input groups from inheriting border styles from table cells when placed within a table

  // Undo padding and float of grid classes
  &[class*="col-"] {
    float: none;
    padding-left: 0;
    padding-right: 0;
  }

  .binf-form-control {
    position: relative;
    z-index: 2;
    float: left;

    width: 100%;
    margin-bottom: 0;
  }
}


.binf-input-group-lg > .binf-form-control,
.binf-input-group-lg > .binf-input-group-addon,
.binf-input-group-lg > .binf-input-group-btn > .binf-btn {
  .binf-input-lg();
}
.binf-input-group-sm > .binf-form-control,
.binf-input-group-sm > .binf-input-group-addon,
.binf-input-group-sm > .binf-input-group-btn > .binf-btn {
  .binf-input-sm();
}


// Display as table-cell
// -------------------------
.binf-input-group-addon,
.binf-input-group-btn,
.binf-input-group .binf-form-control {
  display: table-cell;

  &:not(:first-child):not(:last-child) {
    border-radius: 0;
  }
}
// Addon and addon wrapper for buttons
.binf-input-group-addon,
.binf-input-group-btn {
  width: 1%;
  white-space: nowrap;
  vertical-align: middle; // Match the inputs
}

// Text input groups
// -------------------------
.binf-input-group-addon {
  padding: @padding-base-vertical @padding-base-horizontal;
  font-size: @font-size-base;
  font-weight: normal;
  line-height: 1;
  color: @input-color;
  text-align: center;
  background-color: @input-group-addon-bg;
  border: 1px solid @input-group-addon-border-color;
  border-radius: @border-radius-base;

  // Sizing
  &.binf-input-sm {
    padding: @padding-small-vertical @padding-small-horizontal;
    font-size: @font-size-small;
    border-radius: @border-radius-small;
  }
  &.binf-input-lg {
    padding: @padding-large-vertical @padding-large-horizontal;
    font-size: @font-size-large;
    border-radius: @border-radius-large;
  }

  input[type="radio"],
  input[type="checkbox"] {
    margin-top: 0;
  }
}

// Reset rounded corners
.binf-input-group .binf-form-control:first-child,
.binf-input-group-addon:first-child,
.binf-input-group-btn:first-child > .binf-btn,
.binf-input-group-btn:first-child > .binf-btn-group > .binf-btn,
.binf-input-group-btn:first-child > .binf-dropdown-toggle,
.binf-input-group-btn:last-child > .binf-btn:not(:last-child):not(.binf-dropdown-toggle),
.binf-input-group-btn:last-child > .binf-btn-group:not(:last-child) > .binf-btn {
  .border-right-radius(0);
}
.binf-input-group-addon:first-child {
  border-right: 0;
}
.binf-input-group .binf-form-control:last-child,
.binf-input-group-addon:last-child,
.binf-input-group-btn:last-child > .binf-btn,
.binf-input-group-btn:last-child > .binf-btn-group > .binf-btn,
.binf-input-group-btn:last-child > .binf-dropdown-toggle,
.binf-input-group-btn:first-child > .binf-btn:not(:first-child),
.binf-input-group-btn:first-child > .binf-btn-group:not(:first-child) > .binf-btn {
  .border-left-radius(0);
}
.binf-input-group-addon:last-child {
  border-left: 0;
}

// Button input groups
// -------------------------
.binf-input-group-btn {
  position: relative;
  font-size: 0;
  white-space: nowrap;
  > .binf-btn {
    position: relative;
    + .binf-btn {
      margin-left: -1px;
    }
    // Bring the "active" button to the front
    &:hover,
    &:focus,
    &:active {
      z-index: 2;
    }
  }

  // Negative margin to only have a 1px border between the two
  &:first-child {
    > .binf-btn,
    > .binf-btn-group {
      margin-right: -1px;
    }
  }
  &:last-child {
    > .binf-btn,
    > .binf-btn-group {
      margin-left: -1px;
    }
  }
}
