
fieldset {
  padding: 0;
  margin: 0;
  border: 0;
  min-width: 0;
}

legend {
  display: block;
  width: 100%;
  padding: 0;
  margin-bottom: @line-height-computed;
  font-size: (@font-size-base * 1.5);
  line-height: inherit;
  color: @legend-color;
  border: 0;
  border-bottom: 1px solid @legend-border-color;
}

label {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 5px;
  font-weight: bold;
}



// Override content-box in Normalize (* isn't specific enough)
input[type="search"] {
  .box-sizing(border-box);
}

// Position radios and checkboxes better
input[type="radio"],
input[type="checkbox"] {
  margin: 4px 0 0;
  margin-top: 1px \9; // IE8-9
  line-height: normal;
}

// Set the height of file controls to match text inputs
input[type="file"] {
  display: block;
}

// Make range inputs behave like textual form controls
input[type="range"] {
  display: block;
  width: 100%;
}

// Make multiple select elements height not fixed
select[multiple],
select[size] {
  height: auto;
}

// Focus for file, radio, and checkbox
input[type="file"]:focus,
input[type="radio"]:focus,
input[type="checkbox"]:focus {
  .tab-focus();
}

// Adjust output element
output {
  display: block;
  padding-top: (@padding-base-vertical + 1);
  font-size: @font-size-base;
  line-height: @line-height-base;
  color: @input-color;
}



.binf-form-control {
  display: block;
  width: 100%;
  height: @input-height-base; // Make inputs at least the height of their button counterpart (base line-height + padding + border)
  padding: @padding-base-vertical @padding-base-horizontal;
  font-size: @font-size-base;
  line-height: @line-height-base;
  color: @input-color;
  background-color: @input-bg;
  background-image: none;
  border: 1px solid @input-border;
  border-radius: @input-border-radius;
  .box-shadow(inset 0 1px 1px rgba(0,0,0,.075));
  .transition(~"border-color ease-in-out .15s, box-shadow ease-in-out .15s");

  // Customize the `:focus` state to imitate native WebKit styles.
  .form-control-focus();

  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    cursor: @cursor-disabled;
    background-color: @input-bg-disabled;
    opacity: 1;
  }

  // Reset height for `textarea`s
  textarea& {
    height: auto;
  }
}

input, textarea {  
  // Placeholder
  .placeholder();
}

input[type="search"] {
  -webkit-appearance: none;
}



@media screen and (-webkit-min-device-pixel-ratio: 0) {
  input[type="date"],
  input[type="time"],
  input[type="datetime-local"],
  input[type="month"] {
    line-height: @input-height-base;

    &.binf-input-sm,
    .binf-input-group-sm & {
      line-height: @input-height-small;
    }

    &.binf-input-lg,
    .binf-input-group-lg & {
      line-height: @input-height-large;
    }
  }
}



.binf-form-group {
  margin-bottom: 15px;
}


.binf-radio,
.binf-checkbox {
  position: relative;
  display: block;
  margin-top: 10px;
  margin-bottom: 10px;

  label {
    min-height: @line-height-computed; // Ensure the input doesn't jump when there is no text
    padding-left: 20px;
    margin-bottom: 0;
    font-weight: normal;
    cursor: pointer;
  }
}
.binf-radio input[type="radio"],
.binf-radio-inline input[type="radio"],
.binf-checkbox input[type="checkbox"],
.binf-checkbox-inline input[type="checkbox"] {
  position: absolute;
  margin-left: -20px;
  margin-top: 4px \9;
}

.binf-radio + .binf-radio,
.binf-checkbox + .binf-checkbox {
  margin-top: -5px; // Move up sibling radios or checkboxes for tighter spacing
}

// Radios and checkboxes on same line
.binf-radio-inline,
.binf-checkbox-inline {
  display: inline-block;
  padding-left: 20px;
  margin-bottom: 0;
  vertical-align: middle;
  font-weight: normal;
  cursor: pointer;
}
.binf-radio-inline + .binf-radio-inline,
.binf-checkbox-inline + .binf-checkbox-inline {
  margin-top: 0;
  margin-left: 10px; // space out consecutive inline controls
}

input[type="radio"],
input[type="checkbox"] {
  &[disabled],
  &.binf-disabled,
  fieldset[disabled] & {
    cursor: @cursor-disabled;
  }
}
// These classes are used directly on <label>s
.binf-radio-inline,
.binf-checkbox-inline {
  &.binf-disabled,
  fieldset[disabled] & {
    cursor: @cursor-disabled;
  }
}
// These classes are used on elements with <label> descendants
.binf-radio,
.binf-checkbox {
  &.binf-disabled,
  fieldset[disabled] & {
    label {
      cursor: @cursor-disabled;
    }
  }
}



.binf-form-control-static {
  // Size it appropriately next to real form controls
  padding-top: (@padding-base-vertical + 1);
  padding-bottom: (@padding-base-vertical + 1);
  // Remove default margin from `p`
  margin-bottom: 0;

  &.binf-input-lg,
  &.binf-input-sm {
    padding-left: 0;
    padding-right: 0;
  }
}


.binf-input-sm {
  .input-size(@input-height-small; @padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @input-border-radius-small);
}
.binf-form-group-sm {
  .binf-form-control {
    .input-size(@input-height-small; @padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @input-border-radius-small);
  }
  .binf-form-control-static {
    height: @input-height-small;
    padding: @padding-small-vertical @padding-small-horizontal;
    font-size: @font-size-small;
    line-height: @line-height-small;
  }
}

.binf-input-lg {
  .input-size(@input-height-large; @padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @input-border-radius-large);
}
.binf-form-group-lg {
  .binf-form-control {
    .input-size(@input-height-large; @padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @input-border-radius-large);
  }
  .binf-form-control-static {
    height: @input-height-large;
    padding: @padding-large-vertical @padding-large-horizontal;
    font-size: @font-size-large;
    line-height: @line-height-large;
  }
}


.binf-has-feedback {
  // Enable absolute positioning
  position: relative;

  // Ensure icons don't overlap text
  .binf-form-control {
    padding-right: (@input-height-base * 1.25);
  }
}

.binf-form-control-feedback {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2; // Ensure icon is above input groups
  display: block;
  width: @input-height-base;
  height: @input-height-base;
  line-height: @input-height-base;
  text-align: center;
  //pointer-events: none;
}
.binf-input-lg + .binf-form-control-feedback {
  width: @input-height-large;
  height: @input-height-large;
  line-height: @input-height-large;
}
.binf-input-sm + .binf-form-control-feedback {
  width: @input-height-small;
  height: @input-height-small;
  line-height: @input-height-small;
}

// Feedback states
.binf-has-success {
  .form-control-validation(@state-success-text; @state-success-text; @state-success-bg);
}
.binf-has-warning {
  .form-control-validation(@state-warning-text; @state-warning-text; @state-warning-bg);
}
.binf-has-error {
  .form-control-validation(@state-danger-text; @state-danger-text; @state-danger-bg);
}

// Reposition feedback icon if input has visible label above
.binf-has-feedback label {

  & ~ .binf-form-control-feedback {
     top: (@line-height-computed + 5); // Height of the `label` and its margin
  }
  &.binf-sr-only ~ .binf-form-control-feedback {
     top: 0;
  }
}



.binf-help-block {
  display: inline-block;
  margin-bottom: 3px;
  padding-top: 6px;
  font-size: 11px;
  line-height: 12px;
  margin-left: 40%;
  color: lighten(@text-color, 25%); // lighten the text some for contrast
}

.cs-form-set .binf-help-block {
  margin-left: 0;
}


.binf-form-inline {

  // Kick in the inline
  @media (min-width: @screen-sm-min) {
    // Inline-block all the things for "inline"
    .binf-form-group {
      display: inline-block;
      margin-bottom: 0;
      vertical-align: middle;
    }

    .binf-form-control {
      display: inline-block;
      width: auto;
      vertical-align: middle;
    }

    // Make static controls behave like regular ones
    .binf-form-control-static {
      display: inline-block;
    }

    .binf-input-group {
      display: inline-table;
      vertical-align: middle;

      .binf-input-group-addon,
      .binf-input-group-btn,
      .binf-form-control {
        width: auto;
      }
    }

    // Input groups need that 100% width though
    .binf-input-group > .binf-form-control {
      width: 100%;
    }

    .binf-control-label {
      margin-bottom: 0;
      vertical-align: middle;
    }


    .binf-radio,
    .binf-checkbox {
      display: inline-block;
      margin-top: 0;
      margin-bottom: 0;
      vertical-align: middle;

      label {
        padding-left: 0;
      }
    }
    .binf-radio input[type="radio"],
    .binf-checkbox input[type="checkbox"] {
      position: relative;
      margin-left: 0;
    }

    // Re-override the feedback icon.
    .binf-has-feedback .binf-form-control-feedback {
      top: 0;
    }
  }
}



.binf-form-horizontal {
  .binf-radio,
  .binf-checkbox,
  .binf-radio-inline,
  .binf-checkbox-inline {
    margin-top: 0;
    margin-bottom: 0;
    padding-top: (@padding-base-vertical + 1); // Default padding plus a border
  }
  // Account for padding we're adding to ensure the alignment and of help text
  // and other content below items
  .binf-radio,
  .binf-checkbox {
    min-height: (@line-height-computed + (@padding-base-vertical + 1));
  }

  // Make form groups behave like rows
  .binf-form-group {
    .make-row();
  }

  @media (min-width: @screen-sm-min) {
    .binf-control-label {
      text-align: right;
      margin-bottom: 0;
      padding-top: (@padding-base-vertical + 1); // Default padding plus a border
    }
  }


  .binf-has-feedback .binf-form-control-feedback {
    right: (@grid-gutter-width / 2);
  }


  .binf-form-group-lg {
    @media (min-width: @screen-sm-min) {
      .binf-control-label {
        padding-top: ((@padding-large-vertical * @line-height-large) + 1);
      }
    }
  }
  .binf-form-group-sm {
    @media (min-width: @screen-sm-min) {
      .binf-control-label {
        padding-top: (@padding-small-vertical + 1);
      }
    }
  }
}
